<template>
  <div class="form-wrap">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
      <el-form-item label="" prop="">
        <div>
          <el-radio v-model="ruleForm.integralGetStatus" label="1"></el-radio>
          <!-- <el-input v-model="ruleForm.integralGet.fee"  @blur="formatInputFee($event)"></el-input> -->
          <el-input-number v-model="ruleForm.integralGet.fee" :precision="1" :controls="false" :max="10000.0" :min="0.1"></el-input-number>
          <label> </label>
          <el-input-number v-model="ruleForm.integralGet.integral" :precision="1" :controls="false" :max="10000.0" :min="0.1"></el-input-number>
          <!-- <el-input v-model="ruleForm.integralGet.integral"  @keyup.native="formatInput($event)"></el-input> --><label></label>
          <el-select v-model="ruleForm.integralGetStrategy" placeholder="">
            <el-option v-for="item in cateOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </div>
        <div>
          <el-radio v-model="ruleForm.integralGetStatus" label="0" class="margin-20"></el-radio>
        </div>
      </el-form-item>
      <div class="form-child" v-if="ruleForm.integralGetStatus == 1">
        <el-form-item label="" prop="">
          <div class="radio-cell">
            <el-radio v-model="ruleForm.integralGetDiscountControl" label="1"></el-radio>
          </div>
          <div class="radio-cell margin-20">
            <el-radio v-model="ruleForm.integralGetDiscountControl" label="2"></el-radio>
            <!-- <el-input v-model="ruleForm.integralGetDiscountControlLower" @change="formatInputChange($event)"></el-input> -->
            <el-input-number v-model="ruleForm.integralGetDiscountControlLower" :precision="2" :controls="false" :max="10.0" :min="0.01"></el-input-number>
            <label></label>
            <!-- <el-input v-model="ruleForm.integralGetDiscountControlUpper" @change="formatInputChange($event)"></el-input> -->
            <el-input-number v-model="ruleForm.integralGetDiscountControlUpper" :precision="2" :controls="false" :max="10.0" :min="0.01"></el-input-number>
            <label></label>
            <span style="font-size: 13px;color: #828282;line-height: 1;padding-left: 10px;">0.01-10.00</span>
          </div>
        </el-form-item>
        <el-form-item label="" prop="name" class="padding-0"> <el-input style="padding-left: 0" v-model="ruleForm.integralGetFreezeDays" @keyup.native="formatInput($event)"></el-input><label></label> </el-form-item>
        <el-form-item label="" prop="name" class="padding-0"> <el-input style="padding-left: 0" v-model="ruleForm.effectiveTime" @keyup.native="formatInput($event)"></el-input><label></label> </el-form-item>
      </div>
      <el-form-item label="" prop="region" class="margin-20">
        <div>
          <el-radio v-model="ruleForm.integralUseStatus" label="1"></el-radio>
          <!-- <el-input v-model="ruleForm.integralUse.integral"  @keyup.native="formatInput($event)"></el-input> -->
          <el-input-number v-model="ruleForm.integralUse.integral" :precision="1" :controls="false" :max="10000.0" :min="0.1"></el-input-number>
          <label> </label>
          <el-input-number v-model="ruleForm.integralUse.fee" :precision="1" :controls="false" :max="10000.0" :min="0.1"></el-input-number>
          <!-- <el-input v-model="ruleForm.integralUse.fee"  @blur="formatInputFee($event)"></el-input> -->
          <label></label>
        </div>
        <div class="margin-20">
          <el-radio v-model="ruleForm.integralUseStatus" label="0"></el-radio>
        </div>
      </el-form-item>
      <el-form-item label="">
        <label></label>
        <el-input-number v-model="ruleForm.integralCost.integral" :precision="1" :controls="false" :max="10000.0" :min="0.1"></el-input-number>
        <!-- <el-input v-model="ruleForm.integralCost.integral"  @keyup.native="formatInput($event)"></el-input> -->
        <label> </label>
        <!-- <el-input v-model="ruleForm.integralCost.fee"  @blur="formatInputFee($event)"></el-input> -->
        <el-input-number v-model="ruleForm.integralCost.fee" :precision="1" :controls="false" :max="10000.0" :min="0.1"></el-input-number>
        <label></label>
      </el-form-item>
      <div class="form-child margin-20" v-if="ruleForm.integralUseStatus == 1">
        <el-form-item label="" prop="region">
          <div class="radio-cell">
            <el-radio v-model="ruleForm.integralUseDiscountControl" label="1"></el-radio>
          </div>
          <div class="radio-cell margin-20">
            <el-radio v-model="ruleForm.integralUseDiscountControl" label="2"></el-radio>
            <!-- <el-input v-model="ruleForm.integralUseDiscountControlLower" @change="formatInputChange($event)"></el-input> -->
            <el-input-number v-model="ruleForm.integralUseDiscountControlLower" :precision="2" :controls="false" :max="10.0" :min="0.01"></el-input-number>
            <label></label>
            <!-- <el-input v-model="ruleForm.integralUseDiscountControlUpper" @change="formatInputChange($event)"></el-input> -->
            <el-input-number v-model="ruleForm.integralUseDiscountControlUpper" :precision="2" :controls="false" :max="10.0" :min="0.01"></el-input-number>
            <label></label>
            <span style="font-size: 13px;color: #828282;line-height: 1;padding-left: 10px;">0.01-10.00</span>
          </div>
        </el-form-item>
        <el-form-item label="" prop="name">
          <label> </label>
          <el-input v-model="ruleForm.useProportionalRestriction" @keyup.native="formatInput($event)">
            <template slot="append">
              %
            </template>
          </el-input>
        </el-form-item>
      </div>
      <el-form-item label="" prop="desc" class="margin-20">
        <div class="tinymce-wrap">
          <editor id="tinymce" v-model="ruleForm.integralScript" :init="init"></editor>
        </div>
        <!-- <div v-html='tinymceHtml'></div> -->
      </el-form-item>
      <el-form-item style="margin-bottom: 48px">
        <el-button :disabled="contactBool" type="primary" @click="submitForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script type="text/javascript">
import { postRequest, postForm } from '@/api/api';
import { log } from '@/utils/index.js';

import strLength from '@/common/js/strlen';
import tinymce from 'tinymce/tinymce';
import 'tinymce/themes/modern/theme';
import Editor from '@tinymce/tinymce-vue';
import 'tinymce/plugins/image';
import 'tinymce/plugins/link';
import 'tinymce/plugins/code';
import 'tinymce/plugins/table';
import 'tinymce/plugins/lists';
import 'tinymce/plugins/contextmenu';
import 'tinymce/plugins/wordcount';
import 'tinymce/plugins/colorpicker';
import 'tinymce/plugins/textcolor';
export default {
  name: 'pointSystem',
  props: {
    pointInfo: Object,
    projectName: String,
    contactBool: Boolean
  },
  data() {
    return {
      repProjectName: 'gic-clique',

      // form
      cateOptions: [
        {
          value: '1',
          label: ''
        },
        {
          value: '2',
          label: ''
        },
        {
          value: '3',
          label: ''
        }
      ],
      ruleForm: {
        // 
        memberIntegralId: '', // id
        enterpriseId: '', //id
        integralGet: { fee: '1', integral: '1' }, //  
        integralUse: { fee: '1', integral: '1' }, //
        integralGetStrategy: '1',
        integralScript: '', //
        integralGetStatus: '1', //01
        integralUseStatus: '1', //01
        effectiveTime: '100', //
        integralGetDiscountControl: '2',
        integralGetDiscountControlUpper: ' 0.0', //   
        integralGetDiscountControlLower: '0.0', //   
        integralGetFreezeDays: '1', // 
        integralUseDiscountControl: '1', //  1 2
        integralUseDiscountControlUpper: '0.0',
        integralUseDiscountControlLower: '0.0',
        useProportionalRestriction: '90', //
        integralCost: { fee: '1', integral: '1' } //
      },
      rules: {},

      // tinymce
      tinymceHtml: '',
      init: {
        language_url: 'static/tinymce/zh_CN.js',
        language: 'zh_CN',
        skin_url: 'static/tinymce/skins/lightgray',
        height: 300,
        // 
        // without images_upload_url set, Upload tab won't show up
        // images_upload_url: '',
        // images_upload_base_path: '/some/basepath',
        images_upload_credentials: true, //cookie
        // images_upload_handlerblobInfo, success, failure
        images_upload_handler: (blobInfo, success, failure) => {
          // log(blobInfo)
          this.handleImgUpload(blobInfo, success, failure);
        },
        // 
        plugins: 'link lists image code table colorpicker textcolor wordcount contextmenu',
        toolbar: 'bold italic underline strikethrough | fontsizeselect | forecolor backcolor | alignleft aligncenter alignright alignjustify | bullist numlist | outdent indent blockquote | undo redo | link unlink image code | removeformat',
        branding: false,
        setup: function(editor) {
          // 
          editor.on('click', function(e) {
            log('Editor was clicked');
          });
        }
      }
    };
  },
  created() {
    // this.selectReplayStyle()
  },
  methods: {
    // 
    submitForm(formName) {
      let that = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          // log(that.pointInfo);
          // log(that.ruleForm);
          let data = JSON.parse(JSON.stringify(that.ruleForm));

          // 
          let flag = !data.integralGet.fee || !data.integralGet.integral;
          if (data.integralGetStatus == 1 && flag) {
            that.$message.error('');
            return;
          }

          if (data.integralGetStatus == 1) {
            if (!Number(data.integralGet.fee)) {
              that.$message.error('0');
              return;
            }
            if (!Number(data.integralGet.integral)) {
              that.$message.error('0');
              return;
            }
          }
          // log(String(data.integralGetFreezeDays));

          if (String(data.integralGetFreezeDays) == '') {
            // 
            that.$message.error({
              duration: 1000,
              message: ''
            });
            return;
          }

          if (data.effectiveTime == '') {
            // 
            that.$message.error({
              duration: 1000,
              message: ''
            });
            return;
          } else {
            if (parseInt(data.effectiveTime) < 1) {
              that.$message.error({
                duration: 1000,
                message: ''
              });
              return;
            }
          }

          let tag = !data.integralUse.fee || !data.integralUse.integral || !data.integralCost.integral || !data.integralCost.fee || !parseInt(data.useProportionalRestriction);
          if (data.integralUseStatus == 1 && tag) {
            that.$message.error({
              duration: 1000,
              message: ''
            });
            return;
          }
          if (data.integralUseStatus == 1) {
            if (!Number(data.integralUse.fee)) {
              that.$message.error('0');
              return;
            }
            if (!Number(data.integralUse.integral)) {
              that.$message.error('0');
              return;
            }
          }
          // 
          if (!Number(data.integralCost.fee)) {
            that.$message.error('0');
            return;
          }
          if (!Number(data.integralCost.integral)) {
            that.$message.error('0');
            return;
          }

          // 
          if (data.integralGetStatus == 1 && data.integralGetDiscountControl == 2) {
            let flagA = Number(data.integralGetDiscountControlUpper) <= Number(data.integralGetDiscountControlLower);
            if (!data.integralGetDiscountControlLower || !data.integralGetDiscountControlUpper) {
              that.$message.error('');
              return;
            }
            if (flagA) {
              that.$message.error('');
              return;
            }
          }
          if (data.integralUseStatus == 1 && data.integralUseDiscountControl == 2) {
            let flagB = Number(data.integralUseDiscountControlUpper) <= Number(data.integralUseDiscountControlLower);
            if (!data.integralUseDiscountControlLower || !data.integralUseDiscountControlUpper) {
              that.$message.error('');
              return;
            }
            if (flagB) {
              that.$message.error('');
              return;
            }
          }

          if (data.useProportionalRestriction > 100) {
            that.$message.error('');
            return;
          }

          // 10
          if (!data.integralGetStrategy || data.integralGetStrategy == 0) {
            data.integralGetStrategy = '1';
          }
          data.integralGet.fee = parseInt(data.integralGet.fee * 10);
          data.integralGet.integral = parseInt(data.integralGet.integral * 10);
          data.integralGet = JSON.stringify(data.integralGet);
          data.integralUse = JSON.stringify(data.integralUse);
          data.integralCost = JSON.stringify(data.integralCost);
          that.saveInfo(data);
        } else {
          // log('error submit!!');
          return false;
        }
      });
    },

    saveInfo(data) {
      let that = this;
      let param = data;
      param.requestProject = that.repProjectName;
      // log(param)
      postRequest('/api-admin/save-member-integral-setting', param)
        .then(res => {
          // log(res,res.data,res.data.errorCode)
          let resData = res.data;
          if (resData.errorCode == 0) {
            that.$message({
              message: '',
              type: 'success'
            });
            // 
            // that.updateData();
            return;
          }
          // that.userFormVisible = false;
          that.$message.error({
            duration: 1000,
            message: resData.message
          });
        })
        .catch(function(error) {
          // log(error);
          // that.toLogin()
          // that.userFormVisible = false;
          that.$message.error({
            duration: 1000,
            message: error.message
          });
        });
    },

    // 
    handleImgUpload(blobInfo, success, failure) {
      let that = this;
      let formdata = new FormData();
      formdata.set('upload_file', blobInfo.blob());
      formdata.set('requestProject', that.repProjectName);
      //log(formdata)
      postForm('/api-plug/upload-img', formdata)
        .then(res => {
          success(res.data.result[0].qcloudImageUrl);
        })
        .catch(res => {
          //log(res)
          failure('error');
        });
    },

    // 
    formatInput(e) {
      let that = this;
      that.ruleForm.integralGetFreezeDays = strLength.getCharVal(String(that.ruleForm.integralGetFreezeDays).replace(/[^\d]/g, ''), 4);
      that.ruleForm.effectiveTime = strLength.getCharVal(String(that.ruleForm.effectiveTime).replace(/[^\d]/g, ''), 4);

      that.ruleForm.useProportionalRestriction = strLength.getCharVal(String(that.ruleForm.useProportionalRestriction).replace(/[^\d]/g, ''), 3) || '0';
      if (parseInt(that.ruleForm.useProportionalRestriction) > 100) {
        that.ruleForm.useProportionalRestriction = 100;
      }

      that.$forceUpdate();
    }
  },
  watch: {
    projectName: function(newData, oldData) {
      let that = this;
      // log(":",newData,oldData)
      that.repProjectName = newData || 'gic-web';
    },
    pointInfo: function(newData, oldData) {
      let that = this;
      // log(":",newData,oldData)
      if (newData) {
        that.ruleForm = newData;
      }
    }
  },
  components: {
    Editor
  },
  mounted() {
    let that = this;
    tinymce.init({});
    // 
    if (that.pointInfo) {
      if (Object.keys(that.pointInfo).length) {
        that.ruleForm = that.pointInfo;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.form-wrap {
  .el-input,
  /deep/ .el-input-number {
    width: 178px;
    padding: 0 10px;
    .el-input__inner {
      text-align: left;
    }
  }

  /deep/ .el-form {
    .el-form-item:last-child {
      /*margin-bottom: 0;*/
    }
  }

  .padding-0 {
    padding: 0;
  }

  .form-child {
    width: 100%;
    padding: 15px 20px 1px 0px;
    background: #f5f6f9;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .margin-20 {
    margin-top: 20px;
  }

  .tinymce-wrap {
    width: 890px;

    /deep/ .mce-tinymce {
      -webkit-box-shadow: none;
      -moz-box-shadow: none;
      box-shadow: none;
      border: 1px solid #dcdfe6;
    }

    /deep/ .mce-panel {
      border: 0px solid #dcdfe6;
    }
  }
}
</style>



